html,
body {
  overflow-x: hidden;
  height: 100%;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

.content_first_line {
  border: none;
  height: 5px;
  width: 10%;
  margin: auto;
  box-shadow: 0px 5px #e9ba23;
}

.nav {
  width: 100%;
  background: #fe4e00;
  display: flex;
}

.nav_logo {
  padding: 1rem 1rem;
  margin-left: 7rem;
  flex: none;
}

.menu_bars {
  display: none;
}

.icon_menu_bars {
  display: none;
}

.logo {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
  font-style: italic;
  font-weight: bold;
  font-size: 30px;
}

.space {
  flex: 1;
}

.navbar {
  padding: 1rem 1rem;
  margin-right: 7rem;
  flex: none;
}

.navbar_links {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
  font-weight: 200;
  font-size: 20px;
  margin-left: 1rem;
}

.container {
  background: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1)),
    url("../images/cover2.avif");
  height: 100vh;
  background-position: center;
  background-size: cover;
}

.content {
  height: calc(100vh -80px);
  max-height: 100%;
  width: 100vw;
  padding: 0rem calc((100vw - 1300px) / 2);
}

.items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
  max-height: 100%;
  padding: 0 2rem;
  width: 650px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1;
  font-weight: bold;
}

.text_h1 {
  font-size: clamp(2.5rem, 10vw, 5rem);
  margin-bottom: 2rem;
  padding-right: 1rem;
  padding-bottom: 1rem;
  box-shadow: 3px 10px #e9ba23;
  letter-spacing: 3px;
}

.text_p {
  font-size: clamp(2rem, 2.5vw, 3rem);
  margin-bottom: 2rem;
}

.button {
  font-size: 1.4rem;
  padding: 1rem 4rem;
  border: none;
  background: #e31837;
  color: #fff;
  transition: 0.2s ease-out;
}

.button:hover {
  background: #ff687a;
  transition: 0.2s ease-out;
  cursor: pointer;
  color: #2b2b2b;
}

/* Media Query */
@media screen and (max-width: 575px) {
  .menu_bars {
    position: relative;
    display: block;
    background-image: url("../images/bars.svg");
    background-size: contain;
    height: 40px;
    width: 40px;
    cursor: pointer;
    top: 0;
    right: -3rem;
    transform: translate(-50%, 25%);
  }

  .nav_logo {
    display: flex;
    justify-content: center;
  }

  .navbar {
    display: none;
  }

  .items {
    width: 80%;
  }
  .content_first_line {
    width: 20%;
  }
}
